import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import { AppBar, Toolbar, Typography, Button, Container, Paper, IconButton, Tooltip } from '@mui/material';
import { Brightness4, Brightness7, Settings } from '@mui/icons-material';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import './App.css';
import SearchPage from './components/SearchPage';
import AddPartPage from './components/AddPartPage';
import ApiContext from './context/ApiContext';

function App() {
  const [darkMode, setDarkMode] = useState(false);
  const [apiUrl, setApiUrl] = useState(localStorage.getItem('apiUrl') || '');

  // Create theme based on dark mode preference
  const theme = createTheme({
    palette: {
      mode: darkMode ? 'dark' : 'light',
      primary: {
        main: '#1976d2',
      },
      secondary: {
        main: '#dc004e',
      },
      background: {
        default: darkMode ? '#303030' : '#f5f5f5',
        paper: darkMode ? '#424242' : '#ffffff',
      },
    },
  });

  // Toggle dark mode
  const handleThemeToggle = () => {
    setDarkMode(!darkMode);
  };

  // Set API URL
  const handleApiUrlChange = (url) => {
    console.log('Setting API URL to:', url);
    
    // Validate and fix URL if needed
    let fixedUrl = url;
    if (fixedUrl && !fixedUrl.startsWith('https://')) {
      console.warn('API URL does not start with https://, fixing...');
      fixedUrl = 'https://' + fixedUrl.replace(/^http:\/\//, '');
      console.log('Fixed API URL:', fixedUrl);
    }
    
    localStorage.setItem('apiUrl', fixedUrl);
    setApiUrl(fixedUrl);
    
    // Log the stored value to verify
    console.log('Stored API URL in localStorage:', localStorage.getItem('apiUrl'));
  };

  // Configure API URL
  const configureApiUrl = () => {
    const currentUrl = apiUrl || '';
    console.log('Current API URL:', currentUrl);
    
    const url = prompt('Enter the Google Apps Script Web App URL:', currentUrl);
    console.log('User entered URL:', url);
    
    if (url !== null) {
      // Add a test to verify the URL is accessible
      console.log('Testing URL accessibility...');
      
      // Create a test image to check if the domain is accessible
      const img = new Image();
      img.onload = () => {
        console.log('Domain is accessible!');
        handleApiUrlChange(url);
        alert('API URL updated and domain is accessible!');
      };
      img.onerror = () => {
        console.error('Domain is NOT accessible!');
        
        // Extract domain for testing
        let domain = url;
        try {
          const urlObj = new URL(url);
          domain = urlObj.origin;
        } catch (e) {
          console.error('Invalid URL format:', e);
        }
        
        console.log('Testing domain:', domain);
        alert(`Warning: The domain ${domain} appears to be inaccessible. This may cause JSONP requests to fail. Please check your URL and ensure the Google Apps Script is published as a web app with proper permissions.`);
        
        // Still update the URL as the user requested
        handleApiUrlChange(url);
      };
      
      // Try to load a favicon or other resource from the domain
      try {
        const urlObj = new URL(url);
        img.src = `${urlObj.origin}/favicon.ico?_=${Date.now()}`;
      } catch (e) {
        console.error('Invalid URL format:', e);
        handleApiUrlChange(url);
      }
    }
  };

  // Function to make JSONP requests to the Google Apps Script backend
  const fetchFromAPI = (action, params = {}) => {
    // Create a unique callback name
    const callbackName = 'jsonp_callback_' + Math.round(100000 * Math.random());
    
    // Create the full URL with parameters
    let url = `${apiUrl}?action=${action}`;
    
    // Add other parameters
    Object.keys(params).forEach(key => {
      url += `&${key}=${encodeURIComponent(params[key])}`;
    });
    
    // Add callback parameter and random parameter to prevent caching
    url += `&callback=${callbackName}&_=${new Date().getTime()}`;
    
    // Create a promise to handle the JSONP request
    return new Promise((resolve, reject) => {
      // Define the callback function
      window[callbackName] = (data) => {
        // Clean up by removing the script tag and deleting the callback function
        document.body.removeChild(script);
        delete window[callbackName];
        
        // Resolve the promise with the data
        resolve(data);
      };
      
      // Create a script element
      const script = document.createElement('script');
      script.src = url;
      
      // Handle errors
      script.onerror = () => {
        // Clean up
        document.body.removeChild(script);
        delete window[callbackName];
        
        // Reject the promise with an error
        reject(new Error('Failed to load script'));
      };
      
      // Add the script to the page
      document.body.appendChild(script);
    });
  };

  return (
    <ApiContext.Provider value={{ apiUrl, setApiUrl: handleApiUrlChange, fetchFromAPI }}>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <Router basename={process.env.PUBLIC_URL}>
          <AppBar position="static">
            <Toolbar>
              <Typography variant="h6" style={{ flexGrow: 1 }}>
                Part Number Registry
              </Typography>
              <Button color="inherit" component={Link} to="/">
                Search
              </Button>
              <Button color="inherit" component={Link} to="/add">
                Add Part
              </Button>
              <Tooltip title="Configure API URL">
                <IconButton color="inherit" onClick={configureApiUrl}>
                  <Settings />
                </IconButton>
              </Tooltip>
              <Tooltip title={darkMode ? "Switch to Light Mode" : "Switch to Dark Mode"}>
                <IconButton color="inherit" onClick={handleThemeToggle}>
                  {darkMode ? <Brightness7 /> : <Brightness4 />}
                </IconButton>
              </Tooltip>
            </Toolbar>
          </AppBar>
          
          <Container maxWidth="lg" style={{ marginTop: '2rem', marginBottom: '2rem' }}>
            <Routes>
              <Route path="/" element={<SearchPage />} />
              <Route path="/add" element={<AddPartPage />} />
              <Route path="*" element={
                <Paper style={{ padding: '2rem', textAlign: 'center' }}>
                  <Typography variant="h4" gutterBottom>API Configuration Required</Typography>
                  <Typography paragraph>Please configure the Google Apps Script Web App URL to use this application.</Typography>
                  <Button 
                    variant="contained" 
                    color="primary" 
                    onClick={configureApiUrl}
                  >
                    Configure API URL
                  </Button>
                </Paper>
              } />
            </Routes>
          </Container>
        </Router>
      </ThemeProvider>
    </ApiContext.Provider>
  );
}

export default App;
