import React, { useState, useEffect, useContext } from 'react';
import {
  Paper,
  TextField,
  Button,
  Grid,
  Typography,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  CircularProgress,
  Snackbar,
  Divider,
  Box
} from '@mui/material';
import { Alert } from '@mui/material';
import { Save } from '@mui/icons-material';
import api from '../services/api';
import ApiContext from '../context/ApiContext';
import { highLevelCategories, getLowLevelCategories } from '../data/categories';

const AddPartForm = () => {
  const { apiUrl } = useContext(ApiContext);
  const [subcategories, setSubcategories] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(null);
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  
  // Form state
  const [formData, setFormData] = useState({
    highLevelCategory: '',
    highLevelCategoryDescription: '',
    lowLevelCategory: '',
    lowLevelCategoryDescription: '',
    partIdentifier: '',
    config: '',
    partNumber: '',
    description: '',
    engineer: '',
    manufacturer: '',
    manufacturerPartNumber: '',
    vendorPN: '',
    vendor: '',
    vendorPNLink: ''
  });

  // State for part number validation
  const [partNumberExists, setPartNumberExists] = useState(false);
  const [validatingPartNumber, setValidatingPartNumber] = useState(false);

  // Update subcategories when category changes
  useEffect(() => {
    if (formData.highLevelCategory) {
      const highLevelCat = highLevelCategories.find(cat => cat.code === formData.highLevelCategory);
      
      if (highLevelCat) {
        // Update category description
        setFormData(prev => ({
          ...prev,
          highLevelCategoryDescription: highLevelCat.description
        }));
        
        // Get subcategories for this category
        const subcats = getLowLevelCategories(formData.highLevelCategory);
        setSubcategories(subcats);
      } else {
        setSubcategories([]);
      }
    } else {
      setSubcategories([]);
    }
    
    // Reset subcategory when category changes
    setFormData(prev => ({
      ...prev,
      lowLevelCategory: '',
      lowLevelCategoryDescription: '',
      partIdentifier: '' // Reset part identifier when category changes
    }));
  }, [formData.highLevelCategory]);

  // Get next part identifier when subcategory changes
  useEffect(() => {
    const fetchNextPartIdentifier = async () => {
      if (formData.highLevelCategory && formData.lowLevelCategory && apiUrl) {
        try {
          setLoading(true);
          console.log('Fetching next part identifier for:', formData.highLevelCategory, formData.lowLevelCategory);
          
          const response = await api.getNextPartIdentifier(
            apiUrl,
            formData.highLevelCategory,
            formData.lowLevelCategory
          );
          
          console.log('Next part identifier API response:', response);
          
          if (response.status === 'success' && response.nextPartIdentifier) {
            console.log('Setting next part identifier to:', response.nextPartIdentifier);
            setFormData(prev => ({
              ...prev,
              partIdentifier: response.nextPartIdentifier
            }));
          } else {
            console.error('Failed to get next part identifier:', response);
          }
        } catch (error) {
          console.error('Error fetching next part identifier:', error);
        } finally {
          setLoading(false);
        }
      }
    };
    
    fetchNextPartIdentifier();
  }, [formData.highLevelCategory, formData.lowLevelCategory, apiUrl]);

  // Generate part number when form fields change
  useEffect(() => {
    if (formData.highLevelCategory && formData.lowLevelCategory && formData.partIdentifier) {
      // Generate part number with or without config
      const partNumber = formData.config 
        ? `IM-${formData.highLevelCategory}-${formData.lowLevelCategory}-${formData.partIdentifier}-${formData.config}`
        : `IM-${formData.highLevelCategory}-${formData.lowLevelCategory}-${formData.partIdentifier}`;
      
      setFormData(prev => ({
        ...prev,
        partNumber
      }));
    }
  }, [formData.highLevelCategory, formData.lowLevelCategory, formData.partIdentifier, formData.config]);

  // Check if base part number exists when form fields change
  useEffect(() => {
    if (formData.highLevelCategory && formData.lowLevelCategory && formData.partIdentifier) {
      // Check if base part number exists and update config accordingly
      checkBasePartNumberExists();
    }
  }, [formData.highLevelCategory, formData.lowLevelCategory, formData.partIdentifier]);

  // Check if part identifier exists when manually entered
  useEffect(() => {
    const checkPartIdentifier = async () => {
      // Only check if part identifier is 4 digits and all fields are filled
      if (
        formData.highLevelCategory && 
        formData.lowLevelCategory && 
        formData.partIdentifier && 
        /^\d{4}$/.test(formData.partIdentifier) && 
        apiUrl
      ) {
        try {
          setLoading(true);
          const response = await api.checkPartIdentifier(
            apiUrl,
            formData.highLevelCategory,
            formData.lowLevelCategory,
            formData.partIdentifier
          );
          
          if (response.status === 'success') {
            if (response.exists) {
              // If part identifier exists, update config to next available
              setFormData(prev => ({
                ...prev,
                config: response.nextConfig
              }));
              
              // Show a message to the user
              setError(`Part identifier ${formData.partIdentifier} already exists. Config updated to ${response.nextConfig}.`);
              setSnackbarOpen(true);
            }
          }
        } catch (error) {
          console.error('Error checking part identifier:', error);
        } finally {
          setLoading(false);
        }
      }
    };
    
    // Debounce the check to avoid too many API calls
    const timeoutId = setTimeout(checkPartIdentifier, 500);
    return () => clearTimeout(timeoutId);
  }, [formData.partIdentifier, formData.highLevelCategory, formData.lowLevelCategory, apiUrl]);

  // Update subcategory description when subcategory changes
  useEffect(() => {
    if (formData.lowLevelCategory) {
      const subcategory = subcategories.find(sub => sub.code === formData.lowLevelCategory);
      if (subcategory) {
        setFormData(prev => ({
          ...prev,
          lowLevelCategoryDescription: subcategory.description
        }));
      }
    }
  }, [formData.lowLevelCategory, subcategories]);

  // Check if base part number (without config) already exists
  const checkBasePartNumberExists = async () => {
    if (!formData.highLevelCategory || !formData.lowLevelCategory || !formData.partIdentifier || !apiUrl) return;
    
    try {
      setValidatingPartNumber(true);
      
      // Create the base part number without config
      const basePartNumber = `IM-${formData.highLevelCategory}-${formData.lowLevelCategory}-${formData.partIdentifier}`;
      console.log('Checking if base part number exists:', basePartNumber);
      
      // Use the search API to check if the base part number already exists
      const searchParams = {
        searchTerm: basePartNumber
      };
      
      const response = await api.searchParts(apiUrl, searchParams);
      
      console.log('Base part number search response:', response);
      
      // Check if there are any results in the array
      const exists = response.status === 'success' && 
                    response.results && 
                    response.results.length > 0;
      
      console.log('Base part number exists:', exists, 'Results length:', response.results ? response.results.length : 0);
      
      // Let the user know if the base part number exists, but don't automatically set config
      if (exists && !formData.config) {
        setError(`Part number ${basePartNumber} already exists. Please add a configuration value.`);
        setSnackbarOpen(true);
        setPartNumberExists(true);
      } else {
        // Only check the full part number if the base part number doesn't exist
        // or if the user has added a configuration
        setTimeout(checkPartNumberExists, 100);
      }
    } catch (error) {
      console.error('Error checking base part number:', error);
    } finally {
      setValidatingPartNumber(false);
    }
  };

  // Check if part number already exists
  const checkPartNumberExists = async () => {
    if (!formData.partNumber || !apiUrl) return;
    
    try {
      setValidatingPartNumber(true);
      
      console.log('Checking if part number exists:', formData.partNumber);
      
      // Use the search API to check if the part number already exists
      const searchParams = {
        searchTerm: formData.partNumber
      };
      
      const response = await api.searchParts(apiUrl, searchParams);
      
      console.log('Part number search response:', response);
      
      // Check if there are any results in the array
      const exists = response.status === 'success' && 
                    response.results && 
                    response.results.length > 0;
      
      setPartNumberExists(exists);
      console.log('Part number exists:', exists, 'Results length:', response.results ? response.results.length : 0);
    } catch (error) {
      console.error('Error checking part number:', error);
    } finally {
      setValidatingPartNumber(false);
    }
  };
  
  // Check for part number existence when part identifier or config changes
  useEffect(() => {
    if (formData.partNumber) {
      const timeoutId = setTimeout(checkPartNumberExists, 500);
      return () => clearTimeout(timeoutId);
    }
  }, [formData.partNumber, apiUrl]);

  // Handle form input changes
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    
    // Apply validation rules
    let validatedValue = value;
    
    if (name === 'partIdentifier') {
      // Limit to 4 digits
      validatedValue = value.replace(/\D/g, '').substring(0, 4);
    } else if (name === 'config') {
      // Limit to 3 digits
      validatedValue = value.replace(/\D/g, '').substring(0, 3);
      // Pad with leading zeros if needed
      if (validatedValue.length > 0) {
        validatedValue = validatedValue.padStart(3, '0');
      }
    }
    
    setFormData(prev => ({
      ...prev,
      [name]: validatedValue
    }));
  };

  // Handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault();
    
    console.log('=== FORM SUBMISSION START ===');
    console.log('API URL:', apiUrl);
    console.log('Form Data:', formData);
    
    if (!apiUrl) {
      console.error('API URL not configured');
      setError('API URL not configured');
      setSnackbarOpen(true);
      return;
    }

    // Prevent submission if part number already exists
    if (partNumberExists) {
      console.error('Part number already exists');
      setError('Part number already exists. Please use a different part identifier or configuration.');
      setSnackbarOpen(true);
      return;
    }

    // Validate required fields
    const requiredFields = [
      'highLevelCategory', 
      'lowLevelCategory', 
      'partIdentifier', 
      'description', 
      'engineer'
    ];
    
    const missingFields = requiredFields.filter(field => !formData[field]);
    
    if (missingFields.length > 0) {
      console.error('Missing required fields:', missingFields);
      setError(`Please fill in all required fields: ${missingFields.join(', ')}`);
      setSnackbarOpen(true);
      return;
    }

    try {
      setLoading(true);
      console.log('Calling api.addPart with URL:', apiUrl);
      
      // Add direct debugging to see if the request is being made
      const scriptElement = document.createElement('script');
      scriptElement.src = `${apiUrl}?action=getData&callback=test_callback_${Date.now()}`;
      
      // Define a temporary callback
      window[`test_callback_${Date.now()}`] = (data) => {
        console.log('Test callback received data:', data);
        document.body.removeChild(scriptElement);
        delete window[`test_callback_${Date.now()}`];
      };
      
      // Handle errors for the test request
      scriptElement.onerror = (error) => {
        console.error('Test script load error:', error);
        document.body.removeChild(scriptElement);
        delete window[`test_callback_${Date.now()}`];
      };
      
      // Add the test script to the page
      document.body.appendChild(scriptElement);
      
      // Now try the actual addPart request
      const response = await api.addPart(apiUrl, formData);
      
      console.log('Response from api.addPart:', response);
      
      if (response && response.status === 'success') {
        console.log('Part added successfully:', response);
        setSuccess(`Part ${response.partNumber || 'unknown'} added successfully`);
        setSnackbarOpen(true);
        
        // Reset validation state
        setPartNumberExists(false);
        
        // Reset form (except categories)
        const highLevelCategory = formData.highLevelCategory;
        const lowLevelCategory = formData.lowLevelCategory;
        
        setFormData({
          highLevelCategory,
          highLevelCategoryDescription: formData.highLevelCategoryDescription,
          lowLevelCategory,
          lowLevelCategoryDescription: formData.lowLevelCategoryDescription,
          partIdentifier: '',
          config: '',
          partNumber: '',
          description: '',
          engineer: '',
          manufacturer: '',
          manufacturerPartNumber: '',
          vendorPN: '',
          vendor: '',
          vendorPNLink: ''
        });
      } else {
        console.error('Failed to add part:', response);
        let errorMessage = 'Unknown error';
        
        if (response) {
          if (typeof response.message === 'string') {
            errorMessage = response.message;
          } else if (response.message) {
            try {
              // Try to stringify the message if it's an object
              errorMessage = JSON.stringify(response.message);
            } catch (e) {
              errorMessage = 'Complex error object';
            }
          } else {
            try {
              // Try to stringify the whole response
              errorMessage = JSON.stringify(response);
            } catch (e) {
              errorMessage = 'Complex response object';
            }
          }
        }
        
        setError('Failed to add part: ' + errorMessage);
        setSnackbarOpen(true);
      }
    } catch (error) {
      console.error('Error in form submission:', error);
      console.error('Error type:', error.constructor.name);
      console.error('Error stack:', error.stack);
      setError('Error adding part: ' + (error.message || 'Unknown error'));
      setSnackbarOpen(true);
    } finally {
      setLoading(false);
      console.log('=== FORM SUBMISSION END ===');
    }
  };

  return (
    <div className="form-container">
      <Paper style={{ padding: '2rem' }}>
        <Typography variant="h5" gutterBottom>
          Add New Part
        </Typography>
        
        <form onSubmit={handleSubmit}>
          <Grid container spacing={3}>
            {/* Part Category Section */}
            <Grid item xs={12}>
              <Typography variant="h6" gutterBottom>
                Part Classification
              </Typography>
              <Divider />
            </Grid>
            
            <Grid item xs={12} md={6}>
              <FormControl fullWidth required>
                <InputLabel id="high-level-category-label">High Level Category</InputLabel>
                <Select
                  labelId="high-level-category-label"
                  name="highLevelCategory"
                  value={formData.highLevelCategory}
                  onChange={handleInputChange}
                  label="High Level Category"
                >
                  <MenuItem value="">
                    <em>Select Category</em>
                  </MenuItem>
                  {highLevelCategories.map(category => (
                    <MenuItem key={category.code} value={category.code}>
                      {category.code} - {category.description}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            
            <Grid item xs={12} md={6}>
              <FormControl fullWidth required disabled={!formData.highLevelCategory}>
                <InputLabel id="low-level-category-label">Low Level Category</InputLabel>
                <Select
                  labelId="low-level-category-label"
                  name="lowLevelCategory"
                  value={formData.lowLevelCategory}
                  onChange={handleInputChange}
                  label="Low Level Category"
                >
                  <MenuItem value="">
                    <em>Select Subcategory</em>
                  </MenuItem>
                  {subcategories.map(subcategory => (
                    <MenuItem key={subcategory.code} value={subcategory.code}>
                      {subcategory.code} - {subcategory.description}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                required
                label="Part Identifier"
                name="partIdentifier"
                value={formData.partIdentifier}
                onChange={handleInputChange}
                helperText="4-digit identifier"
              />
            </Grid>
            
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label="Configuration"
                name="config"
                value={formData.config}
                onChange={handleInputChange}
                inputProps={{ maxLength: 3 }}
                helperText="Optional 3-digit configuration code. Only required if base part number already exists."
              />
            </Grid>
            
            <Grid item xs={12}>
              <TextField
                fullWidth
                label="Part Number"
                value={formData.partNumber}
                InputProps={{
                  readOnly: true,
                  endAdornment: validatingPartNumber ? (
                    <CircularProgress size={20} />
                  ) : null,
                  style: {
                    backgroundColor: formData.partNumber ? 
                      (partNumberExists ? '#ffebee' : '#e8f5e9') : 
                      'transparent'
                  }
                }}
                error={partNumberExists}
                helperText={
                  partNumberExists 
                    ? "This part number already exists in the database" 
                    : "Auto-generated based on selections above"
                }
              />
            </Grid>
            
            {/* Part Details Section */}
            <Grid item xs={12} style={{ marginTop: '20px' }}>
              <Typography variant="h6" gutterBottom>
                Part Details
              </Typography>
              <Divider />
            </Grid>
            
            <Grid item xs={12}>
              <TextField
                fullWidth
                required
                label="Description"
                name="description"
                value={formData.description}
                onChange={handleInputChange}
                multiline
                rows={2}
              />
            </Grid>
            
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                required
                label="Engineer"
                name="engineer"
                value={formData.engineer}
                onChange={handleInputChange}
              />
            </Grid>
            
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label="Manufacturer"
                name="manufacturer"
                value={formData.manufacturer}
                onChange={handleInputChange}
              />
            </Grid>
            
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label="Manufacturer Part Number"
                name="manufacturerPartNumber"
                value={formData.manufacturerPartNumber}
                onChange={handleInputChange}
              />
            </Grid>
            
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label="Vendor"
                name="vendor"
                value={formData.vendor}
                onChange={handleInputChange}
              />
            </Grid>
            
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label="Vendor Part Number"
                name="vendorPN"
                value={formData.vendorPN}
                onChange={handleInputChange}
              />
            </Grid>
            
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label="Vendor Link"
                name="vendorPNLink"
                value={formData.vendorPNLink}
                onChange={handleInputChange}
                placeholder="https://"
              />
            </Grid>
            
            {/* Submit Button */}
            <Grid item xs={12} style={{ marginTop: '20px' }}>
              <Box display="flex" justifyContent="flex-end">
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  size="large"
                  startIcon={<Save />}
                  disabled={loading}
                >
                  {loading ? <CircularProgress size={24} /> : 'Save Part'}
                </Button>
              </Box>
            </Grid>
          </Grid>
        </form>
      </Paper>
      
      <Snackbar
        open={snackbarOpen}
        autoHideDuration={6000}
        onClose={() => setSnackbarOpen(false)}
      >
        <Alert 
          onClose={() => setSnackbarOpen(false)} 
          severity={error ? "error" : "success"}
          sx={{ width: '100%' }}
        >
          {error || success}
        </Alert>
      </Snackbar>
    </div>
  );
};

export default AddPartForm;
