// API service for communicating with Google Apps Script backend using JSONP
const api = {
  // Helper function to make JSONP requests
  _jsonpRequest: (apiUrl, action, params = {}) => {
    // Create a unique callback name
    const callbackName = 'jsonp_callback_' + Math.round(100000 * Math.random());
    
    // Ensure API URL is properly formatted
    let fixedApiUrl = apiUrl;
    if (!fixedApiUrl.startsWith('https://')) {
      console.warn('API URL does not start with https://, fixing...');
      fixedApiUrl = 'https://' + fixedApiUrl.replace(/^http:\/\//, '');
    }
    
    if (fixedApiUrl.endsWith('/')) {
      fixedApiUrl = fixedApiUrl.slice(0, -1);
    }
    
    // Create the full URL with parameters - ensure action is the first parameter
    const actionParam = encodeURIComponent(action.trim());
    let url = `${fixedApiUrl}?action=${actionParam}`;
    
    // Add other parameters
    Object.keys(params).forEach(key => {
      url += `&${key}=${encodeURIComponent(params[key])}`;
    });
    
    // Add callback parameter and random parameter to prevent caching
    url += `&callback=${callbackName}&_=${new Date().getTime()}`;
    
    console.log('Making JSONP request to:', url);
    console.log('URL length:', url.length);
    console.log('Action parameter:', actionParam);
    
    // Create a promise to handle the JSONP request
    return new Promise((resolve, reject) => {
      // Define the callback function
      window[callbackName] = (data) => {
        try {
          console.log(`JSONP callback ${callbackName} received data:`, data);
          
          // Clean up by removing the script tag and deleting the callback function
          document.body.removeChild(script);
          delete window[callbackName];
          
          // Resolve the promise with the data
          resolve(data);
        } catch (error) {
          console.error('Error in JSONP callback:', error);
          reject(error);
        }
      };
      
      // Create a script element
      const script = document.createElement('script');
      script.src = url;
      
      // Handle errors
      script.onerror = (error) => {
        console.error('Script load error:', error);
        
        // Clean up
        document.body.removeChild(script);
        delete window[callbackName];
        
        // Reject the promise with an error
        reject(new Error('Failed to load script'));
      };
      
      // Add the script to the page
      document.body.appendChild(script);
      
      // Set a timeout to prevent hanging requests
      setTimeout(() => {
        if (window[callbackName]) {
          try {
            document.body.removeChild(script);
          } catch (e) {
            console.error('Error removing script element:', e);
          }
          delete window[callbackName];
          reject(new Error('Request timed out'));
        }
      }, 10000); // 10 second timeout
    });
  },

  // Get all parts data
  getAllParts: async (apiUrl) => {
    try {
      if (!apiUrl) {
        throw new Error('API URL is not set. Please configure it in settings.');
      }
      
      const response = await api._jsonpRequest(apiUrl, 'getData');
      return response;
    } catch (error) {
      console.error('Error fetching parts data:', error);
      throw error;
    }
  },

  // Get category hierarchy
  getCategories: async (apiUrl) => {
    try {
      if (!apiUrl) {
        throw new Error('API URL is not set. Please configure it in settings.');
      }
      
      const response = await api._jsonpRequest(apiUrl, 'getCategories');
      return response;
    } catch (error) {
      console.error('Error fetching categories:', error);
      throw error;
    }
  },

  // Search parts
  searchParts: async (apiUrl, searchParams) => {
    try {
      if (!apiUrl) {
        throw new Error('API URL is not set. Please configure it in settings.');
      }
      
      // Extract search parameters
      const { highLevelCategory, lowLevelCategory, searchTerm } = searchParams;
      
      // Log the parameters being sent
      console.log('Sending search request with parameters:', {
        term: searchTerm || '',
        category: highLevelCategory || '',
        subcategory: lowLevelCategory || ''
      });
      
      const response = await api._jsonpRequest(apiUrl, 'search', {
        term: searchTerm || '',
        category: highLevelCategory || '',
        subcategory: lowLevelCategory || ''
      });
      
      console.log('Search response:', response);
      
      // Ensure results is always an array
      if (response && response.status === 'success') {
        if (!response.results || !Array.isArray(response.results)) {
          response.results = [];
        }
      }
      
      return response;
    } catch (error) {
      console.error('Error searching parts:', error);
      throw error;
    }
  },

  // Get next part identifier for a category/subcategory
  getNextPartIdentifier: async (apiUrl, category, subcategory) => {
    try {
      console.log('Getting next part identifier for', category, subcategory);
      
      if (!apiUrl || !category || !subcategory) {
        console.error('Missing required parameters');
        return {
          status: 'error',
          message: 'API URL, category, and subcategory are required'
        };
      }
      
      const response = await api._jsonpRequest(apiUrl, 'getMaxPartIdentifier', {
        category,
        subcategory
      });
      
      console.log('Next part identifier response:', response);
      return response;
    } catch (error) {
      console.error('Error getting next part identifier:', error);
      return {
        status: 'error',
        message: error.message || 'Unknown error occurred'
      };
    }
  },
  
  // Check if a part identifier exists and get next config if it does
  checkPartIdentifier: async (apiUrl, category, subcategory, partIdentifier) => {
    try {
      console.log('Checking part identifier', partIdentifier, 'for', category, subcategory);
      
      if (!apiUrl || !category || !subcategory || !partIdentifier) {
        console.error('Missing required parameters');
        return {
          status: 'error',
          message: 'API URL, category, subcategory, and partIdentifier are required'
        };
      }
      
      const response = await api._jsonpRequest(apiUrl, 'checkPartIdentifier', {
        category,
        subcategory,
        partIdentifier
      });
      
      console.log('Check part identifier response:', response);
      return response;
    } catch (error) {
      console.error('Error checking part identifier:', error);
      return {
        status: 'error',
        message: error.message || 'Unknown error occurred'
      };
    }
  },

  // Add new part - SIMPLIFIED VERSION
  addPart: async (apiUrl, partData) => {
    try {
      console.log('=== ADD PART DEBUG START ===');
      
      if (!apiUrl) {
        throw new Error('API URL is not set. Please configure it in settings.');
      }
      
      // Verify API URL format and fix if needed
      let fixedApiUrl = apiUrl;
      if (!fixedApiUrl.startsWith('https://')) {
        console.warn('API URL does not start with https://, fixing...');
        fixedApiUrl = 'https://' + fixedApiUrl.replace(/^http:\/\//, '');
      }
      
      if (fixedApiUrl.endsWith('/')) {
        fixedApiUrl = fixedApiUrl.slice(0, -1);
      }
      
      console.log('Using API URL:', fixedApiUrl);
      
      // Map form field names to spreadsheet column names
      const mappedData = {
        "Part Number": partData.partNumber || "",
        "Description": partData.description || "",
        "Eng": partData.engineer || "",
        "A - High Level Category": partData.highLevelCategory || "",
        "A - High Level Category Description": partData.highLevelCategoryDescription || "",
        "B - Lower Level Category": partData.lowLevelCategory || "",
        "B - Lower Level Category Description": partData.lowLevelCategoryDescription || "",
        "CCCC - Part Identifier": partData.partIdentifier || "",
        "FFF - Config": partData.config || "", 
        "Manufacturer": partData.manufacturer || "",
        "Manufacturer Part Number": partData.manufacturerPartNumber || "",
        "Vendor": partData.vendor || "",
        "Vendor PN": partData.vendorPN || "",
        "Vendor PN / Link": partData.vendorPNLink || ""
      };
      
      console.log('Mapped data:', mappedData);
      
      // Use the _jsonpRequest helper for consistency
      return await api._jsonpRequest(fixedApiUrl, 'addPart', { data: JSON.stringify(mappedData) });
    } catch (error) {
      console.error('Error adding part:', error);
      return {
        status: 'error',
        message: error.message || 'Unknown error occurred'
      };
    }
  },
};

export default api;
