import React, { useState, useEffect, useContext } from 'react';
import {
  Container,
  Grid,
  Paper,
  Typography,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  CircularProgress,
  Snackbar,
  Box,
  Link
} from '@mui/material';
import { Alert } from '@mui/material';
import { Search as SearchIcon } from '@mui/icons-material';
import api from '../services/api';
import ApiContext from '../context/ApiContext';
import { highLevelCategories, getLowLevelCategories } from '../data/categories';

const SearchPage = () => {
  const { apiUrl } = useContext(ApiContext);
  const [selectedCategory, setSelectedCategory] = useState('');
  const [selectedSubcategory, setSelectedSubcategory] = useState('');
  const [searchTerm, setSearchTerm] = useState('');
  const [subcategories, setSubcategories] = useState([]);
  const [searchResults, setSearchResults] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [snackbarOpen, setSnackbarOpen] = useState(false);

  // Update subcategories when category changes
  useEffect(() => {
    if (selectedCategory) {
      // Get subcategories for the selected category
      const subcats = getLowLevelCategories(selectedCategory);
      setSubcategories(subcats);
    } else {
      setSubcategories([]);
    }
    setSelectedSubcategory('');
  }, [selectedCategory]);

  // Handle category selection change
  const handleCategoryChange = (event) => {
    setSelectedCategory(event.target.value);
  };

  // Handle subcategory selection change
  const handleSubcategoryChange = (event) => {
    setSelectedSubcategory(event.target.value);
  };

  // Handle search term change
  const handleSearchTermChange = (event) => {
    setSearchTerm(event.target.value);
  };

  // Handle search form submission
  const handleSearch = async (event) => {
    event.preventDefault();
    
    if (!apiUrl) {
      setError('API URL not configured');
      setSnackbarOpen(true);
      return;
    }

    try {
      setLoading(true);
      
      // Prepare search parameters
      const searchParams = {
        highLevelCategory: selectedCategory,
        lowLevelCategory: selectedSubcategory,
        searchTerm
      };
      
      console.log('Sending search params:', searchParams);
      
      const response = await api.searchParts(apiUrl, searchParams);
      
      console.log('Search response received:', response);
      
      if (response.status === 'success') {
        console.log('Search results data:', response.results);
        setSearchResults(response.results || []);
        if (!response.results || response.results.length === 0) {
          setError('No parts found matching your criteria');
          setSnackbarOpen(true);
        }
      } else {
        setError('Failed to search parts: ' + response.message);
        setSnackbarOpen(true);
      }
    } catch (error) {
      console.error('Search error:', error);
      setError('Error searching parts: ' + error.message);
      setSnackbarOpen(true);
      setSearchResults([]);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container maxWidth="lg">
      <Paper style={{ padding: '2rem', marginBottom: '2rem' }}>
        <Typography variant="h5" gutterBottom>
          Search Parts
        </Typography>
        
        <form onSubmit={handleSearch}>
          <Grid container spacing={3}>
            <Grid item xs={12} md={4}>
              <FormControl fullWidth>
                <InputLabel id="category-label">High Level Category</InputLabel>
                <Select
                  labelId="category-label"
                  value={selectedCategory}
                  onChange={handleCategoryChange}
                  label="High Level Category"
                >
                  <MenuItem value="">
                    <em>All Categories</em>
                  </MenuItem>
                  {highLevelCategories.map(category => (
                    <MenuItem key={category.code} value={category.code}>
                      {category.code} - {category.description}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            
            <Grid item xs={12} md={4}>
              <FormControl fullWidth disabled={!selectedCategory}>
                <InputLabel id="subcategory-label">Low Level Category</InputLabel>
                <Select
                  labelId="subcategory-label"
                  value={selectedSubcategory}
                  onChange={handleSubcategoryChange}
                  label="Low Level Category"
                >
                  <MenuItem value="">
                    <em>All Subcategories</em>
                  </MenuItem>
                  {subcategories.map(subcategory => (
                    <MenuItem key={subcategory.code} value={subcategory.code}>
                      {subcategory.code} - {subcategory.description}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            
            <Grid item xs={12} md={4}>
              <TextField
                fullWidth
                label="Search Term"
                value={searchTerm}
                onChange={handleSearchTermChange}
                placeholder="Part number, description, etc."
              />
            </Grid>
            
            <Grid item xs={12}>
              <Box display="flex" justifyContent="flex-end">
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  startIcon={<SearchIcon />}
                  disabled={loading}
                >
                  {loading ? <CircularProgress size={24} /> : 'Search'}
                </Button>
              </Box>
            </Grid>
          </Grid>
        </form>
      </Paper>
      
      {/* Search Results */}
      <Paper style={{ padding: '2rem' }}>
        <Typography variant="h5" gutterBottom>
          Search Results
        </Typography>
        
        {loading ? (
          <Box display="flex" justifyContent="center" my={4}>
            <CircularProgress />
          </Box>
        ) : searchResults.length > 0 ? (
          <TableContainer>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>Part Number</TableCell>
                  <TableCell>Description</TableCell>
                  <TableCell>Engineer</TableCell>
                  <TableCell>Manufacturer</TableCell>
                  <TableCell>Vendor</TableCell>
                  <TableCell>Link</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {searchResults.map((part, index) => (
                  <TableRow key={index}>
                    <TableCell>{part["Part Number"]}</TableCell>
                    <TableCell>{part["Description"]}</TableCell>
                    <TableCell>{part["Engineer"]}</TableCell>
                    <TableCell>{part["Manufacturer"]}</TableCell>
                    <TableCell>{part["Vendor"]}</TableCell>
                    <TableCell>
                      {part["Vendor PN / Link"] ? (
                        <Link href={part["Vendor PN / Link"]} target="_blank" rel="noopener noreferrer">
                          View
                        </Link>
                      ) : part["Vendor Link"] ? (
                        <Link href={part["Vendor Link"]} target="_blank" rel="noopener noreferrer">
                          View
                        </Link>
                      ) : (
                        '-'
                      )}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        ) : (
          <Typography variant="body1" align="center" my={4}>
            No results to display. Use the search form above to find parts.
          </Typography>
        )}
      </Paper>
      
      <Snackbar
        open={snackbarOpen}
        autoHideDuration={6000}
        onClose={() => setSnackbarOpen(false)}
      >
        <Alert 
          onClose={() => setSnackbarOpen(false)} 
          severity="error"
          sx={{ width: '100%' }}
        >
          {error}
        </Alert>
      </Snackbar>
    </Container>
  );
};

export default SearchPage;
